.comp-daily .section-inner {
  padding-top: 0 !important;
}
.comp-daily .section-inner ul li {
  padding: 5px;
  border-bottom: 1px solid rgba(142, 209, 241, 0.5);
}
.comp-daily .section-inner ul li a {
  position: relative;
  display: block;
  padding: 10px;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a.active {
  background: var(--sky);
  color: #fff;
}
.comp-daily .section-inner ul li a.active .fa-arrow-down {
  transform: translateY(-50%) rotate(180deg);
}
.comp-daily .section-inner ul li a .fa-arrow-down {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.3em;
}
.comp-daily .section-inner ul li a .date {
  margin-bottom: 10px;
  font-family: 500;
}
.comp-daily .section-inner ul li a .date span {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .date span.desc {
  font-style: italic;
}
.comp-daily .section-inner ul li a .date .fa-arrow-down {
  float: right;
}
.comp-daily .section-inner ul li a .info img {
  display: inline-block;
  margin-right: 25px;
  vertical-align: middle;
  width: 50px;
}
.comp-daily .section-inner ul li a .info span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li a .info span i {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .info span em {
  font-weight: 500;
}
.comp-daily .section-inner ul li a.active {
  background: var(--sky);
  color: #fff;
}
.comp-daily .section-inner ul li a.active i {
  transform: rotate(180deg);
}
.comp-daily .section-inner ul li .detail {
  display: none;
  color: #fff;
  padding: 10px;
  background: rgba(65, 117, 158, 0.6);
}
.comp-daily .section-inner ul li .detail p {
  padding: 7px 0;
  border-bottom: 1px solid #fff;
}
.comp-daily .section-inner ul li .detail p:last-child {
  border: none;
}
.comp-daily .section-inner ul li .detail p span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li .detail p span i {
  font-size: 0.85em;
}
.comp-daily .section-inner ul li .detail p span b {
  margin: 0 5px;
}
.comp-daily .section-inner ul li .detail p span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail p span em small {
  font-size: 0.85em;
}