@use "mixin" as m;

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

html {
  /*   margin-top: 0 !important; */
  --sKy: #8ed1f1;
  --color: #41759e;
}

body {
  height: 100vh;
  background-color: var(--sKy);
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/weather-icos.gif);
    background-size: 900px;
    opacity: 0.3;
    animation: bg 5000s linear infinite;
    @keyframes bg {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: -50000px -50000px;
      }
    }
  }
}

h1 {
  perspective: 700px;
  position: absolute;
  left: 50%;
  top: 0;
  width: min(50%, 200px);
  transform: translate(-50%, -50%);
  img {
    width: 100%;
    opacity: 0.7;
    animation: rotate 10s infinite cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  @keyframes rotate {
    0% {
      transform: rotateX(0deg);
    }
    50% {
      transform: rotateX(360deg);
    }
    100% {
      transform: rotateX(360deg);
    }
  }
}

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -45%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(#fff, 0.7);
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid rgba(#fff, 0.5);
  animation: grow 1.5s infinite alternate linear;
  @media screen and (min-width: 900px) {
    min-height: 760px;
  }
  @keyframes grow {
    0% {
      box-shadow: 0 0 5px 0px rgba(#fff, 0.3);
    }
    100% {
      box-shadow: 0 0 15px 5px rgba(#fff, 0.7);
    }
  } //keyframes
  .app-inner {
    height: 100%;
    position: relative;
    @media screen and (min-width: 900px) {
      display: flex;
      flex-wrap: wrap;
      overflow: hidden;
    }
    @media screen and (max-width: 900px) {
      overflow-x: hidden;
    }
    section {
      box-sizing: border-box;
      @media screen and (min-width: 900px) {
        width: 50%;
        height: 50%;
      }
      @media screen and (max-width: 900px) {
        margin: 20px 0;
      }
      .section-inner {
        position: relative;
        border-radius: 10px;
        box-sizing: border-box;
        padding: 10px;
        min-height: 250px;
        @media screen and (min-width: 900px) {
          height: 100%;
          overflow: auto;
        } //media
        .preloader {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          background: var(--sKy);
          border-radius: 10px;
        } //loader
        h2 {
          text-transform: uppercase;
          font-weight: 600;
          padding: 5px 10px;
          background: var(--color);
          color: #fff;
          border-radius: 5px;
          @media screen and (min-width:900px) {
            position: sticky;
            top: 0px;
            left: 0px;
            z-index: 1;
          }

        }
        .loader {
          font-size: 10px;
          text-indent: -9999em;
          width: 5em;
          height: 5em;
          border-radius: 50%;
          position: relative;
          -webkit-animation: load3 1.4s infinite linear;
          animation: load3 1.4s infinite linear;
          -webkit-transform: translateZ(0);
          -ms-transform: translateZ(0);
          transform: scale(0.5) translateZ(0);
        }
        .loader:before {
          width: 50%;
          height: 50%;
          background: #ffffff;
          border-radius: 100% 0 0 0;
          position: absolute;
          top: 0;
          left: 0;
          content: "";
        }
        .loader:after {
          background: #0dc5c1;
          width: 75%;
          height: 75%;
          border-radius: 50%;
          content: "";
          margin: auto;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
        }
        @-webkit-keyframes load3 {
          0% {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
          }
          100% {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
          }
        }
        @keyframes load3 {
          0% {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
          }
          100% {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
          }
        }
      } //section-inner
    } //section
  } //app-inner

  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color);
    }
  } //media hover
} //root

.app-inner,
.section-inner {
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--sKy);
    }
  } //media hover
} //#root inner
