@use "mixin" as m;

.comp-daily {
  .section-inner {
    padding-top: 0 !important;
    ul {
      li {
        padding: 5px;
        border-bottom: 1px solid rgba(#8ed1f1, 0.5);
        a {
          position: relative;
          display: block;
          padding: 10px;
          transition: all 0.5s;
          &.active {
            background: var(--sky);
            color: #fff;
            .fa-arrow-down {
              transform: translateY(-50%) rotate(180deg);
            }
          }
          .fa-arrow-down {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 1.3em;
          }
          .date {
            margin-bottom: 10px;
            font-family: 500;
            span {
              margin-right: 5px;
              &.desc {
                font-style: italic;
              } //desc
            } //span
            .fa-arrow-down {
              float: right;
            } //fa-arrow-down
          } //date

          .info {
            img {
              display: inline-block;
              margin-right: 25px;
              vertical-align: middle;
              width: 50px;
            } //img
            span {
              margin-right: 15px;
              i {
                margin-right: 5px;
              } //i
              em {
                font-weight: 500;
              } //em
            } //span
          } //info
          &.active {
            background: var(--sky);
            color: #fff;
            i {
              transform: rotate(180deg);
            }
          }
        } //a
        .detail {
          display: none;
          color: #fff;
          padding: 10px;
          background: rgba(#41759e, 0.6);
          p {
            padding: 7px 0;
            border-bottom: 1px solid #fff;
            &:last-child {
              border: none;
            }
            span {
              margin-right: 15px;
              i {
                font-size: 0.85em;
              }
              b {
                margin: 0 5px;
              } //b
              em {
                font-weight: 600;
                small {
                  font-size: 0.85em;
                } //small
              } //em
            } //span
          } //p
        } //detail
        &.active {
        }
      } //li
    } //ul
  } //section-inner
} //comp-daily
