@use "mixin" as m;

.comp-hourly {
  .section-inner {
    padding-top: 0 !important;
    overflow: auto;
    h2 {
      position: sticky;
      left: 0;
    }
    @media screen and (min-width: 900px) {
      display: flex;
      flex-direction: column;
    } //min-width
    .wrap {
      position: relative;
      display: inline-table;
      @media screen and (min-width: 900px) {
        height: 100%;
        /*  overflow: hidden; */
      } // wrap min-width
      @media screen and (max-width: 900px) {
        height: 400px;
      } // wrap max-width
      ul {
        display: inline-flex;
        height: 100%;
        li {
          padding-top: 20px;
          width: 70px;
          height: 100%;
          box-sizing: border-box;
          &:nth-child(even) {
            background: rgba(#fff, 0.5);
          }
          p {
            font-size: 0.9em;
            margin: 3px 0;
            text-align: center;
            img {
              display: inline-block;
              width: 50%;
            } //img
          } //p
        } //li
      } //ul
      .chart-wrap {
        position: absolute;
        bottom: 0;
        left: 5px;
        width: calc(100% - 35px);
        height: 50%;
      } //chart-wrap
    } //wrap
  } //section-inner
} //comp-daily
