@use "./mixin" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 20px !important;
  } //section-inner min

  .section-inner {
    .current-output {
      position: relative;
      box-sizing: border-box;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-size: cover;
      background-position: center;
      color: #fff;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //section-inner min
      @media screen and (max-width: 900px) {
        padding: 10px;
      } //section-inner min

      button.reload {
        z-index: 1;
        position: absolute;
        width: 50px;
        height: 50px;
        right: 5%;
        top: 15%;
        font-size: 1.5em;
        opacity: 0.7;
        transition: all 0.5s;
        &:active,
        &:hover {
          opacity: 1;
          transform: rotate(360deg);
        }
      }
      address {
        display: flex;
        overflow: hidden;
        margin-bottom: 10px;
        width: min(90%, 350px);
        box-sizing: border-box;
        padding: 3px;
        span {
          display: inline-block;
          white-space: nowrap;
          animation: marquee 10s linear infinite ;
          @keyframes marquee {
            0% {
              transform: translate(0);
            }
            100% {
              transform: translate(-100%);
            }
          }
        }
      }
      .circle-container {
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(#000, 0.3);
        @include m.square;
        .sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            width: 100%;
            .sun {
              fill: rgba(255, 196, 0);
              offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
              animation: rotate-sun 10s infinite linear;
              offset-distance: 50%;
              @keyframes rotate-sun {
                0% {
                  transform: rotate(0deg) scale(0.7);
                }
                100% {
                  transform: rotate(360deg) scale(0.6);
                }
              } //keyframes rotate-sun
            } //sun
            .line {
              fill: none;
              stroke: rgba(255, 196, 0);
              stroke-width: 3px;
            } //line
            .dash {
              fill: none;
              stroke: rgba(255, 196, 0);
              stroke-width: 6px;
              stroke-dasharray: 1;
              stroke-dashoffset: 0;
            } //dash
          } //svg
          time {
            font-size: 0.85em;
            padding: 3px 10px;
            background: rgba(255, 196, 0);
            border-radius: 7px;
            display: inline-block;
            color: #fff;
            /*  small {
              font-size: 0.8em;
              font-weight: 600;
              margin-right: 2px;
              color: #fff;
            } //small */
            &.risetime {
              transform: translateX(-30%);
            }
            &.settime {
              float: right;
              transform: translateX(30%);
            }
            div.sunset {
            }
          } //sunset
        } //.circle-inner
        .circle-container-inner {
          @include m.posLT(absolute, 50%, 50%, -50%, -50%);
          width: 90%;

          .temp {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            em {
              font-size: 2.5em;
              margin-right: -60px;
              position: relative;
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup {
                font-size: 0.7em;
              } //sup
            } //em
            img {
              width: 100px;
              -webkit-mask-image: url(../assets/img/back.png);
              -webkit-mask-size: 100%;
              -webkit-mask-position: center;
              -webkit-mask-repeat: no-repeat;
            } //img
            span {
              position: absolute;
              transform: translate(60px, 10px);
              font-weight: 600;
              i {
                margin-right: 3px;
                font-size: 0.8em;
              } //i
            } //span
          } //temp

          .desc {
            margin-top: -20px;
            text-align: center;
            font-size: 1.3em;
          } //desc
          .etc {
            width: 80%;
            margin: 10px auto;
            text-align: center;
            padding: 5px;
            box-sizing: border-box;
            border-radius: 10px;
            background: rgba(#000, 0.3);
            span {
              display: inline-block;
              margin: 3px 5px;
              font-size: 0.9em;
              i {
                margin-right: 3px;
                font-size: 0.8em;
                &.fa-location-arrow {
                  margin-right: 7px;
                } //fa-location-arrow
              } //i
              b {
              } //b
              small {
                font-size: 0.8em;
              } //small
            } //span
          } //etc
          .time-date {
            margin-top: -2px;
            text-align: center;
            color: #fff;
            time {
              display: block;
              &.time {
                font-size: 1.2em;
                small {
                  font-size: 0.8em;
                }
                &.date {
                  font-size: 0.8em;
                }
              } //&.time
            } //time
          } //time-date
        } //circle-container-inner
      } //circle-container
    } //current-output
  } //section-inner
} //section.comp-current
