@use "mixin" as m;

.comp-map {
  @media screen and (max-width: 900px) {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    margin: 0 !important;
    font-size: 1.5em;
    transform: translateX(100%);
    transition: transform 0.5s;
    &.active{
      transform: translateX(0%);
    }
  }
  .btns {
    display: flex;
    position: absolute;
    top: calc(50% - 25px);
    left: -50px;
    width: 100px;
    height: 50px;
    z-index: 1;
    border-radius: 25px;
    overflow: hidden;
    button {
      width: 50px;
      height: 50px;
      color: #fff;
      background: var(--color);
      &:first-child {
        background: red;
      }
      &:last-child {
        background: orange;
      }
    }
    @media screen and (min-width: 900px) {
      display: none;
    } //max-width
  } //btns
  .section-inner {
    @media screen and (max-width: 900px) {
      height: 100%;
    } //max-width
    #map {
      filter: hue-rotate(40deg) brightness(102%);
      height: 100%;
      border-radius: 10px;
    }
  }
}
