body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

html {
  /*   margin-top: 0 !important; */
  --sKy: #8ed1f1;
  --color: #41759e;
}

body {
  height: 100vh;
  background-color: var(--sKy);
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/weather-icos.gif);
  background-size: 900px;
  opacity: 0.3;
  animation: bg 5000s linear infinite;
}
@keyframes bg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -50000px -50000px;
  }
}

h1 {
  perspective: 700px;
  position: absolute;
  left: 50%;
  top: 0;
  width: min(50%, 200px);
  transform: translate(-50%, -50%);
}
h1 img {
  width: 100%;
  opacity: 0.7;
  animation: rotate 10s infinite cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@keyframes rotate {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(360deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

#root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -45%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(255, 255, 255, 0.7);
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  animation: grow 1.5s infinite alternate linear;
}
@media screen and (min-width: 900px) {
  #root {
    min-height: 760px;
  }
}
@keyframes grow {
  0% {
    box-shadow: 0 0 5px 0px rgba(255, 255, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 15px 5px rgba(255, 255, 255, 0.7);
  }
}
#root .app-inner {
  height: 100%;
  position: relative;
}
@media screen and (min-width: 900px) {
  #root .app-inner {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner {
    overflow-x: hidden;
  }
}
#root .app-inner section {
  box-sizing: border-box;
}
@media screen and (min-width: 900px) {
  #root .app-inner section {
    width: 50%;
    height: 50%;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner section {
    margin: 20px 0;
  }
}
#root .app-inner section .section-inner {
  position: relative;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 10px;
  min-height: 250px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner {
    height: 100%;
    overflow: auto;
  }
}
#root .app-inner section .section-inner .preloader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--sKy);
  border-radius: 10px;
}
#root .app-inner section .section-inner h2 {
  text-transform: uppercase;
  font-weight: 600;
  padding: 5px 10px;
  background: var(--color);
  color: #fff;
  border-radius: 5px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner h2 {
    position: sticky;
    top: 0px;
    left: 0px;
    z-index: 1;
  }
}
#root .app-inner section .section-inner .loader {
  font-size: 10px;
  text-indent: -9999em;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: scale(0.5) translateZ(0);
}
#root .app-inner section .section-inner .loader:before {
  width: 50%;
  height: 50%;
  background: #ffffff;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
}
#root .app-inner section .section-inner .loader:after {
  background: #0dc5c1;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@media (hover: hover) {
  #root::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  #root::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  #root::-webkit-scrollbar-thumb {
    background-color: var(--color);
  }
}

@media (hover: hover) {
  .app-inner::-webkit-scrollbar-track,
  .section-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  .app-inner::-webkit-scrollbar,
  .section-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  .app-inner::-webkit-scrollbar-thumb,
  .section-inner::-webkit-scrollbar-thumb {
    background-color: var(--sKy);
  }
}